import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { getHero } from '../services/firebaseService';
import type { Hero as HeroType } from '../services/firebaseService';
import { getStats } from '../services/dataService';
import { Code, Terminal, Github, Database, ArrowRight, ScrollText, Award, Building2, Users } from 'lucide-react';
import { ImageFallback } from './ImageFallback';

const iconMap = {
  Code,
  Terminal,
  Github,
  Database
};

export function Hero() {
  const [heroData, setHeroData] = useState<HeroType | null>(null);
  const [stats, setStats] = useState<Stats | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchHeroData() {
      try {
        const data = await getHero();
        setHeroData(data);
      } catch (error) {
        console.error('Error fetching hero data:', error);
        setError(error instanceof Error ? error.message : 'Failed to load hero data');
      } finally {
        setLoading(false);
      }
    }

    const fetchStats = async () => {
      try {
        const data = await getStats();
        setStats(data);
      } catch (error) {
        console.error('Error fetching stats:', error);
        setError('Failed to load stats');
      } finally {
        setLoading(false);
      }
    };

    fetchStats();
    fetchHeroData();
  }, []);

  const scrollToProjects = () => {
    const projectsSection = document.getElementById('projects');
    projectsSection?.scrollIntoView({ behavior: 'smooth' });
  };

  if (loading) {
    return (
      <section className="relative min-h-screen flex items-center justify-center">
        <div className="text-white text-2xl">Loading...</div>
      </section>
    );
  }

  if (error) {
    return (
      <section className="relative min-h-screen flex items-center justify-center">
        <div className="text-red-500 text-2xl">Error: {error}</div>
      </section>
    );
  }

  return (
    <section className="relative min-h-screen">
      {/* Background with code effect */}
      <div className="absolute inset-0">
        <div className="relative w-full h-full bg-gray-900">
          <div className="absolute inset-0 code-rain-effect opacity-20"></div>
          <div className="absolute inset-0 bg-gradient-to-b from-gray-900/90 via-gray-900/80 to-gray-900/90"></div>
        </div>
      </div>

      {/* Content */}
      <div className="relative min-h-screen flex items-center">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 w-full py-20 md:py-0">
          <div className="max-w-3xl space-y-8">
            {/* Terminal-like introduction */}
            <div className="space-y-4 font-mono">
              <div className="bg-gray-800/50 rounded-lg p-4 backdrop-blur-sm">
                <div className="flex items-center space-x-2 mb-3">
                  <div className="w-3 h-3 rounded-full bg-red-500"></div>
                  <div className="w-3 h-3 rounded-full bg-yellow-500"></div>
                  <div className="w-3 h-3 rounded-full bg-green-500"></div>
                </div>
                <div className="text-green-400">
                  <span className="text-blue-400">const</span> <span className="text-yellow-400">developer</span> = {'{'}
                </div>
                <div className="ml-4">
                  <span className="text-purple-400">name:</span> <span className="text-green-300">"{heroData?.name || 'Your Name'}"</span>,
                </div>
                <div className="ml-4">
                  <span className="text-purple-400">role:</span> <span className="text-green-300">"Software Engineer"</span>,
                </div>
                <div className="ml-4">
                  <span className="text-purple-400">skills:</span> [<span className="text-green-300">"Frontend", "Backend", "Cloud", "DevOps"</span>],
                </div>
                <div className="ml-4">
                  <span className="text-purple-400">status:</span> <span className="text-green-300">"Ready to code"</span>
                </div>
                <div className="text-green-400">{'}'}</div>
                <div className="text-gray-400 mt-2 terminal-cursor">// Let's build something amazing together</div>
              </div>
            </div>

            {/* CTA Buttons */}
            <div className="flex flex-wrap gap-4">
              <Link
                to="/projects"
                className="group relative inline-flex items-center gap-2 bg-green-600 hover:bg-green-700 text-white px-6 py-3 rounded-lg font-mono text-sm transition-all duration-300"
              >
                View Projects <Code className="w-4 h-4" />
              </Link>
              <button
                onClick={scrollToProjects}
                className="group relative inline-flex items-center gap-2 bg-gray-800 hover:bg-gray-700 text-white px-6 py-3 rounded-lg font-mono text-sm transition-all duration-300"
              >
                See My Work <Terminal className="w-4 h-4" />
              </button>
            </div>

            {/* Tech Stack */}
            <div className="pt-12 grid grid-cols-4 gap-4 sm:gap-6">
              {[
                { icon: 'react', label: 'React' },
                { icon: 'node', label: 'Node.js' },
                { icon: 'typescript', label: 'TypeScript' },
                { icon: 'cloud', label: 'Cloud' }
              ].map((tech, index) => (
                <div key={index} className="text-center group bg-gray-800/30 backdrop-blur-sm p-4 rounded-lg hover:bg-gray-800/50 transition-all duration-300">
                  <div className="font-mono text-sm text-gray-400">{tech.label}</div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Scroll Indicator */}
      <div className="absolute left-1/2 -translate-x-1/2 bottom-8 hidden md:flex flex-col items-center animate">
        <div className="w-1 h-10 rounded-full bg-white/20 relative overflow-hidden">
          <div className="absolute top-0 left-0 w-full h-full bg-white/60 transform -translate-y-1/2 animate-scroll"></div>
        </div>
        <span className="text-white/60 text-sm mt-4 tracking-wider uppercase">Scroll Down</span>
      </div>
    </section>
  );
}